<template>
    <v-layout align-start>
        <v-flex class="inicio">
            <v-toolbar  text color="white">
                <v-toolbar-title>Consultas</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
              <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" 
                label="Bsqueda" single-line hide-details></v-text-field>
               <v-spacer></v-spacer>
                 <v-btn color="primary" v-if="verNuevo==0" @click="seleccionarPaciente()" dark class="mb-2" >Nuevo</v-btn>
                </v-toolbar>
                

                 <!--  ....................Seccion de Modales........................... -->
                  

                  <!--  ....................modal para la seleccion de un tratamiento........................... -->
                   <v-dialog v-model="dialog" max-width="1000px">
                      <v-card>
                          <v-card-title>
                            <span class="headline">Seleccione un tratamiento</span>
                          </v-card-title>            
                          <v-card-text>
                            <v-container grid-list-md>
                              <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                  <v-text-field v-model='texto' @keyup.enter="listarTratamientos()" class='text-xs-center' append-icon='search' label='Buscqueda'></v-text-field>
                                  <template>
                                    <v-data-table
                                      :headers="cabeceraTratamientos"
                                      :items="tratamientos"
                                      class="elevation-1"
                                  >
                                      <template v-slot:item.opcioness="{item}">
                                          <v-icon
                                          small
                                          class="mr-2"
                                          @click="agregarDetalle(item)"
                                          >
                                          add
                                          </v-icon>                  
                                      </template>        
                                      <template v-slot:items="props">
                                      <td>{{ props.item.codigo }}</td>
                                      <td>{{ props.item.nombre }}</td>
                                      <td>{{ props.item.categoria.nombre }}</td>
                                      <td>{{ props.item.precio_venta }}</td>
                                      <td>{{ props.item.descripcion }}</td>
                                      </template>
                                      <template v-slot:item.estado="{item}">
                                          <div v-if="item.estado">
                                              <span class="blue--text">Activo</span>
                                          </div>
                                          <div v-else>
                                              <span class="red--text">Inactivo</span>
                                          </div>
                                      </template>             
                                  </v-data-table>
                                  </template>  
                                </v-flex>
                              </v-layout>
                            </v-container>
                          </v-card-text>            
                          <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn class="ma-2" tile outlined color="error" @click="close">Cancelar</v-btn>
                          </v-card-actions>
                      </v-card>
                  </v-dialog>


                 <!--  ....................modal para el  recibo de una consulta........................... -->
                <v-dialog v-model="comprobanteModal" max-width="1000px">
                  <v-card>
                    <v-card-title class="headline" justify="right">
                      <v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>     Recibo de consulta
                    </v-card-title>
                    <v-card-text>
                      <div id="facturai">
                          <header>
                              <div id="logo">
                                  <img src="data:image/jpeg;base64," id="imagen" >
                              </div>
                              <div id="datos">
                                  <p id="encabezado">
                                      <b>Medicina Muscular</b><br>Av.Camara de Comercio por 58 y 48 No.317, Villas la Hacienda, Merida<br>Telefono:9991775676<br>
                                  </p>
                              </div>
                              <div id="fact">
                                  <p>{{tipo_comprobante}}<br>
                                  {{num_comprobante}}<br>
                                  {{format_dateFull(fecha)}}</p>
                              </div>
                          </header>
                          <br>
                          <section>
                              <div>
                                  <table id="fapaciente">
                                      <tbody>
                                          <tr>
                                              <td id="paciente">
                                                  <strong>Sr(a).{{paciente.nombre}}</strong><br>
                                                  <strong>Direccin:</strong>{{paciente.direccion}}<br>
                                                  <strong>Telfono:</strong>{{paciente.telefono}}<br>
                                                  <strong>Email:</strong>{{paciente.email}}
                                              </td>
                                          </tr>
                                      </tbody>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <section>
                              <div>
                                  <table id="factratamientos">
                                      <thead>
                                          <tr id="fa">
                                              <th>CANT</th>
                                              <th>DESCRIPCION</th>
                                              <th>COSTO</th>
                                              <th>DESC.</th>
                                              <th>PRECIO TOTAL</th>
                                          </tr>
                                      </thead>
                                      <tbody>
                                          <tr v-for="det in detalles" :key="det._id">
                                              <td style="text-align:center;">{{det.cantidad}}</td>
                                              <td>{{det.tratamientos}}</td>
                                              <td style="text-align:right;">{{det.precio}}</td>
                                              <td style="text-align:right;">{{det.descuento}}</td>
                                              <td style="text-align:right;">{{(det.cantidad*det.precio)-det.descuento}}</td>
                                          </tr>
                                      </tbody>
                                      <tfoot>
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">SUBTOTAL</th>
                                              <th style="text-align:right;">$ {{totalParcial=(total-totalImpuesto).toFixed(2)}}</th>
                                          </tr>
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">IVA({{impuesto}}%)</th>
                                              <th style="text-align:right;">$ {{totalImpuesto=((total*impuesto)/(1+impuesto)).toFixed(2)}}</th>
                                          </tr>
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">TOTAL</th>
                                              <th style="text-align:right;">$ {{total=calcularTotal}}</th>
                                          </tr>
                                      </tfoot>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <br>
                          <footer>
                              <div id="gracias">
                                  <p><b>Gracias por su preferencia!</b></p>
                              </div>
                          </footer>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="ocultarComprobante()" class="ma-2" tile outlined color="error">Cancelar</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>


                <!-- ....................Modal para seleccionar un paciente.................... -->

                <v-dialog v-model="seleccionarPacienteModal" max-width="1000px">
                  <v-card>
                          <v-card-title>
                            <span class="headline">Seleccione a un paciente</span>
                          </v-card-title>            
                          <v-card-text>
                            <v-container grid-list-md>
                              <v-layout wrap>
                               
                                  <v-flex xs9 sm12 md12 lg11 xl12>
                                  <v-text-field v-model='texto' @keyup.enter="listarPacientes()" class='text-xs-center' append-icon='search' label='Buscqueda'></v-text-field>
                                  </v-flex>
                                  <v-flex  xs1 sm12 md12 lg1 xl12>
                                  <v-btn  color="blue-grey" class="ma-3 white--text" @click="listarPacientes()">
                                   <v-icon dark>search</v-icon>
                                  </v-btn>
                                  </v-flex>
                                  <v-flex xs12 sm12 md12 lg12 xl12>
                                  <template>
                                    <v-data-table
                                      :headers="cabeceraPacientes"
                                      :items="pacientes"
                                      class="elevation-1">

                                      <template v-slot:item.opcion="{item}">
                                          <v-btn  small color="success" class="ma-3 white--text" @click="mostrarNuevo(item)">Seleccionar
                                          </v-btn>                 
                                      </template>        
                                      <template v-slot:items="props">
                    
                                      <td>{{ props.item.nombre }}</td>
                                      <td>{{ props.item.email }}</td>
                                      <td>{{ props.item.telefono }}</td>
                                      </template>            
                                  </v-data-table>
                                  </template>  
                                </v-flex>
                              </v-layout>
                            </v-container>
                          </v-card-text>            
                          <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn class="ma-2" tile outlined color="error" @click="ocultarPacientes()">Cancelar</v-btn>
                          </v-card-actions>
                      </v-card>
                </v-dialog>
        
                <!-- ....................modal Receta.................... -->
              <v-dialog v-model="hoja" max-width="1000px">
                  <v-card>
                    <v-card-title class="headline" justify="right">
                      <v-btn @click="crearPDFreceta()"><v-icon>print</v-icon></v-btn>     Receta Medica
                    </v-card-title>
                    <v-card-text>
                      <div id="contenido">
                          <header>
                              <div id="logoReceta">
                                  <img src="data:image/jpeg;base64," id="imagenReceta" >
                              </div>
                              <div id="datosReceta">
                                  <p id="encabezadoReceta">
                                      <b>Dr. Jorge Luis Ojeda Barrientos</b><br>
                                       Mdico Cirujano - Universidad Anhuac Mayab<br>
                                       Especialista en Medicina del Deporte - UADY<br>
                                       Maestria en Fisioterapia Deportiva - Universidad Modelo<br>
                                       Certificado por el Consejo Nacional de Medicina del Deporte<br>
                                       Fecha: <strong>{{format_dateFull(new Date())}}</strong>
                                  </p>
                              </div>
                          </header>
                          <br>
                          <section>
                              <div>
                                  <table id="fapaciente">
                                      <tbody>
                                        <v-layout wrap>
                                        <v-flex sm12  md6 lg6 x6>
                                           <strong>
                                            <v-text-field label="Nombre del paciente"  readonly v-model="pacientee" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md2 lg2 x2>
                                           <strong>
                                            <v-text-field label="Edad"  readonly v-model="edad" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md2 lg2 x2>
                                           <strong>
                                            <v-text-field label="Peso"  readonly v-model="peso" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md2 lg2 x2>
                                           <strong>
                                            <v-text-field label="Talla"  readonly v-model="talla" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md12 lg12 x12>
                                           <strong>
                                            <v-text-field label="Alergias"  readonly v-model="alergias" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        </v-layout>
                                      </tbody>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <section>
                              <div>
                                  <table id="medicamentos">
                                      <thead>
                                          <tr id="fa">
                                              <th>MEDICAMENTO</th>
                                              <th>DESCRIPCION</th>
                                          </tr>
                                      </thead>
                                      <tbody>
                                          <tr v-for="det in receta" :key="det._id">
                                              <td style="text-align:center;">{{det.medicamento}}</td>
                                              <td style="text-align:center;">{{det.descripcion}}</td>
                                          </tr>
                                      </tbody>
                                 </table>
                              </div>
                          </section>
                          <br>
                          
                          <footer>
                              <div id="gracias">
                                  <p><b>Gracias por su preferencia!</b></p>
                              </div>
                          </footer>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="ocultarReceta()" class="ma-2" tile outlined color="error">Cancelar</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                
            <!--  ....................termina seccion de modales........................... -->
            
            
            <v-data-table
                :headers="headers"
                :items="consultas"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0"
            >
                <template v-slot:item.opciones="{item}">
                   <v-icon
                    small
                    class="mr-2"
                    @click="verConsulta(item)"
                    >
                    tab
                    </v-icon> 
                    <v-icon
                    small
                    class="mr-2"
                    @click="mostrarComprobante(item)"
                    >
                    print
                    </v-icon>
                    <v-icon
                    small
                    class="mr-2"
                    @click="editarConsulta(item)"
                    >
                    edit
                    </v-icon>  

                </template>
                <template v-slot:item.fecha="{item}">
                    <div>
                        <span>{{format_date(item.createdAt)}}</span>
                    </div>
                </template>           
                <template v-slot:items="props">
                <td>{{ props.item.num_comprobante }}</td>
                <td>{{ props.item.usuario.nombre }}</td>
                <td>{{ props.item.paciente.nombre }}</td>
                </template>
                <template v-slot:item.total="{item}">
                    <div>
                        <span>{{formatPrice(item.total)}}</span>
                    </div>
                </template> 
                <template v-slot:item.saldo="{item}">
                    <div>
                        <span>{{formatPrice(item.saldo)}}</span>
                    </div>
                </template> 
                <template v-slot:item.estado="{item}">
                    <div v-if="item.estado != 3">
                        <span class="red--text">Pendiente</span>
                    </div>
                    <div v-else>
                        <span class="green--text">Pagado</span>
                    </div>
                </template>            
                
                <template v-slot:no-data>
                <v-btn color="primary" @click="listar()">Resetear</v-btn>
                </template>
            </v-data-table>


            <!-- seccion para agregar una nueva consulta o ver una consulta -->

            <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
              <v-layout row wrap>
                <v-flex sm12 sm8 md8 lg6 x18>
                  <v-text-field readonly v-model="pacientee" label="Paciente">
                  </v-text-field>
                </v-flex>
                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="headline"><strong>Nota de evolucion</strong></v-card-title>
                  </v-card>
                </v-flex>
                
                 <v-flex xs12 lg4 >
                   <v-textarea
                   
                   background-color="amber lighten-4"
                   filled
                   readonly
                   name="input-7-1"
                   label="Padecimiento Actual"
                   v-model="notaevoArray.experiencia"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea  
                   background-color="amber lighten-4"
                   filled
                   readonly
                   name="input-7-1"
                   label="Diagnostico"
                   v-model="notaevoArray.diagnostico"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea
                   background-color="amber lighten-4"
                   filled
                   readonly
                   name="input-7-1"
                   label="Pronostico"
                   v-model="notaevoArray.pronostico"
                  ></v-textarea>
                </v-flex>
              
                <v-flex xs12 lg4 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="experiencia" 
                   label="Padecimiento Actual"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="diagnostico"
                   label="Diagnostico"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="pronostico"
                   label="Pronostico"
                  ></v-textarea>
                </v-flex>
            

                <!-- ...................Seccion para realizar una receta................. --> 
                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="headline"><strong>Receta Medica</strong><v-spacer></v-spacer> <v-btn color="primary" @click="mostrarReceta(),scrollToElement()"><v-spacer></v-spacer>
                <v-icon >print</v-icon>
              </v-btn></v-card-title>
                 
                  </v-card>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea v-model="medicamento" class="mx-2" label="Medicamento" rows="1"  prepend-icon="local_pharmacy"> 
                   </v-textarea>
                </v-flex>
                <v-flex xs12 lg6 >
                   <v-textarea v-model="descripcion" class="mx-2" label="Descipcion" rows="1" prepend-icon="subject">
                   </v-textarea>
                </v-flex>
                <v-flex xs12 lg2 >
                  <v-btn color="blue-grey" class="ma-3 white--text" v-on:click="DetallesReceta()">
                    Agregar<v-icon dark>add</v-icon>
                  </v-btn>
                </v-flex>
                <v-flex xs12 lg12 >
                   <v-textarea v-model="observacion_receta" class="mx-2" label="Observaciones" rows="1" prepend-icon="create">
                   </v-textarea>
                </v-flex>
                <v-flex xs12 sm2 md2 lg2 xl2  v-show="errorMedicamentos">
                  <div class="red--text" v-text="errorMedicamentos">

                  </div>

                </v-flex>
                 <v-flex xs12 sm12 md12 lg12 xl12>
                  <template>
                    <v-data-table
                      :headers="cabeceraRecetas"
                      :items="receta"
                      hide-default-footer
                      class="elevation-1"
                    >
                  <template v-slot:item.borrar="{item}">
                     <v-icon small class="mr-2" @click="eliminarMedicamento(receta,item)">
                      delete
                     </v-icon>
                    </template>
                    <template v-slot:item.medicamento="props">
                     <v-text-field v-model="props.item.medicamento">
                     </v-text-field>
                    </template>
                   <template v-slot:item.descripcion="props">
                    <v-text-field v-model="props.item.descripcion">
                    </v-text-field>
                   </template>
                  <template v-slot:no-data>
                      <h3>No hay detalles</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

           <!-- ...................Seccion para realizar el cobro................. --> 

                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="headline"><strong>Detalles de cobro</strong></v-card-title>
                  </v-card>
                </v-flex>
                

                <v-flex xs12 sm8 md8 lg10 x8>
                  <v-text-field v-model="codigo" label="codigo" @keyup.enter="buscarCodigo()">                    
                  </v-text-field>
                </v-flex>
                <v-flex xs12 sm2 md2 lg2 x12>
                  <v-btn color="blue-grey" class="ma-3 white--text" @click="mostrarModalTratamientos()">
                    Buscar<v-icon dark>list</v-icon>
                  </v-btn>
                </v-flex>
                <v-flex xs12 sm2 md2 lg2 xl2  v-show="errorTratamientos">
                  <div class="red--text" v-text="errorTratamientos">

                  </div>

                </v-flex>
                <v-flex xs12 sm12 md12 lg12 xl12>
                  <template>
                    <v-data-table
                      :headers="cabeceraDetalles"
                      :items="detalles"
                      hide-default-footer
                      class="elevation-1"
                    >
                    <template v-slot:item.borrar="{item}">
                     <v-icon
                    small
                    class="mr-2"
                    @click="eliminarDetalle(detalles,item)"
                    >
                    delete
                    </v-icon>
                    
                </template>
                    <template v-slot:item.cantidad="props">
           <v-text-field v-model="props.item.cantidad" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.precio="props">
           <v-text-field v-model="props.item.precio" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.descuento="props">
           <v-text-field v-model="props.item.descuento" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.subtotal="props">
          ${{(props.item.cantidad * props.item.precio)-props.item.descuento}}
         </template>
                    <template v-slot:no-data>
                      <h3>No hay tratamientos agregados al detalle</h3>
                    </template>  
                    </v-data-table>
                    <v-flex class="text-xs-right">
                      <strong>Total Neto:</strong> $ {{(total=calcularTotal).toFixed(2)}}
                    </v-flex>
                  </template>
                </v-flex>
                <v-flex xs12 sm12 md12 v-show="valida">
                  <div class="red-text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                </v-flex>
                <v-flex xs12 sm12 md12 xl12>
                  <v-btn class="ma-2" tile outlined color="error" @click.native="ocultarNuevo()">Cancelar</v-btn>
                  <v-btn class="ma-2" tile outlined color="primary" v-if="verDetalle==0" @click.native="guardar()">Guardar</v-btn>
                  
                </v-flex>
              </v-layout>
            </v-container>
            
            <!-- Termina seccion para agregar una nueva consulta o ver una consulta -->
        </v-flex>
    </v-layout>
</template>
<script>
import axios from "axios";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import moment from "moment";
export default {
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      dialog: false,
      search: "",
      consultas: [],
      momentcc: moment().startOf("day"),
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Folio", value: "num_comprobante", sortable: false },
        { text: "Fecha", value: "fecha", sortable: true },
        { text: "Doctor", value: "usuario.nombre", sortable: true },
        { text: "Paciente", value: "paciente.nombre", sortable: true },
        { text: "Total", value: "total", sortable: false },
        { text: "Saldo", value: "saldo", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      _id: "",
      id: "",
      pacientee: "",
      pacienteee:"",
      paciente: "",
      alergias:"",
      edad:"",
      peso:"",
      talla:"",
      pacientes: [],
      nuevaconsulta: [],
      experienciabase:"",
      experiencia:[],
      arrayNotaEvo:[],
      tipo_comprobante: "",
      comprobantes: ["FACTURA", "TICKET"],
      num_comprobante: "",
      experiencia: "",
      nombre:"",
      diagnostico: "",
      pronostico: "",
      medicamento: "",
      descripcion: "",
      observacion_receta: "",
      impuesto: 0.18,
      codigo: "",
      cabeceraDetalles: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Tratamiento", value: "tratamientos", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Sub Total", value: "subtotal", sortable: false }
      ],
      cabeceraRecetas: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Medicamento", value: "medicamento", sortable: false },
        { text: "Descripcion", value: "descripcion", sortable: false }
      ],
      detalles: [],
      listareceta:[],
      receta: [],
      notaevolucion:[],
      notaevoArray:[],
      notaevoConsulta:[],
      verNuevo: 0,
      errorTratamientos: null,
      errorMedicamentos:null,
      total: 0,
      totalParcial: 0,
      totalImpuesto: 0,
      tratamientos: [],
      texto: "",
      cabeceraTratamientos: [
        { text: "Seleccionar", value: "opcioness", sortable: false },
        { text: "Codigo", value: "codigo", sortable: true },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Categoria", value: "categoria.nombre", sortable: true },
        { text: "Precio", value: "precio_venta", sortable: false },
        { text: "Descripcin", value: "descripcion", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      cabeceraPacientes: [
        { text: "Seleccionar", value: "opcion", sortable: false },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Email", value: "email", sortable: true },
        { text: "Telefono", value: "telefono", sortable: false }
      ],
      verDetalle: 0,
      valida: 0,
      validaMensaje: [],
      adAccion: 0,
      adNombre: "",
      adId: "",
      comprobanteModal: 0,
      hoja: 0,
      seleccionarPacienteModal: 0,
      fecha: null
    };
  },
  computed: {
    calcularTotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        resultado =
          resultado +
          (this.detalles[i].cantidad * this.detalles[i].precio -
            this.detalles[i].descuento);
      }
      return resultado;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.listar();
    /* this.selectPacientes(); */
  },
  methods: {
    format_date(value){
         if (value) {
           return moment(String(value)).format('LL')
          }
      },
      format_dateFull(value){
         if (value) {
           moment.locale('es');
           return moment(String(value)).format('LLLL');
          }
      },
      formatPrice(value) {
      let val = (value/1).toFixed(2).replace(/[^\d\.]/g, "")
        return val.toString().replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,")
    },
    scrollToElement() {
    const el = this.$el.getElementsByClassName('inicio')[0];

    if (el) {
      el.scrollIntoView();
    }
  },
    crearPDF() {
      var quotes = document.getElementById("factura");
      html2canvas(quotes).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 200;
        var pageHeight = 255;

        var imgHeight = canvas.height * imgWidth / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteConsulta.pdf");
      });
    },

    formatDate(date) {
      const [day, month, year] = date.split("-");
      return `${day}/${month}/${year}`;
    },

    crearPDFreceta() {
      var quote = document.getElementById("contenido");
      html2canvas(quote).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 200;
        var pageHeight = 200;

        var imgHeight = canvas.height * imgWidth / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteConsulta.pdf");
      });
    },

    mostrarComprobante(item) {
      this.limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.fecha = item.createdAt;
      this.paciente = item.paciente;
      this.impuesto = item.impuesto;
      this.listarDetalles(item._id);
      this.comprobanteModal = 1;
    },
    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("consulta/query?_id=" + id, configuracion)
        .then(function(response) {
          me.detalles = response.data.detalles;
          me.receta = response.data.receta;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
     
    mostrarReceta() {
      this.hoja = 1;
    },
    ocultarComprobante() {
      this.comprobanteModal = 0;
      this.limpiar();
    },
    ocultarReceta() {
      this.hoja = 0;
    },
    ocultarPacientes() {
      this.seleccionarPacienteModal = 0;
      this.limpiar();
    },
    /*  selectPacientes() {
      let me = this;
      let pacienteArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("paciente/listPacientes", configuracion)
        .then(function(response) {
          pacienteArray = response.data;
          pacienteArray.map(function(x) {
            me.pacientes.push({ text: x.nombre,  value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    }, */
    buscarCodigo() {
      let me = this;
      me.errorTratamientos = null;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("tratamientos/queryCodigo?codigo=" + this.codigo, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorTratamientos = "No existe el tratamiento";
        });
    },
    agregarDetalle(data) {
      this.errorTratamientos = null;
      if (this.encuentra(data._id) == true) {
        this.errorTratamientos = "El tratamiento ya ha sido agregado";
      } else {
        this.detalles.push({
          _id: data._id,
          tratamientos: data.nombre,
          cantidad: 1,
          precio: data.precio_venta,
          descuento: 0
        });
        this.codigo = "";
      }
    },
    DetallesReceta(data) {
      if(this.medicamento == ""){
         this.errorMedicamentos = "Agregue un medicamento";
      }else{
      this.receta.push({
        medicamento: this.medicamento,
        descripcion: this.descripcion
      });
      this.errorMedicamentos = null;
      this.medicamento = "";
      this.descripcion = "";
    }},
    encuentra(id) {
      let sw = 0;
      for (var i = 0; i < this.detalles.length; i++) {
        if (this.detalles[i]._id == id) {
          sw = true;
        }
      }
      return sw;
    },
    eliminarDetalle(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },
    eliminarMedicamento(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },
    listarTratamientos() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("tratamientos/list?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.tratamientos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarPacientes() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("paciente/list?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.pacientes = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    verConsulta(item) {
      this.limpiar();
      this.notaevoConsulta = item.notaevolucion[item.notaevolucion.length-1]; 
      this.experiencia = this.notaevoConsulta.experiencia;
      this.diagnostico = this.notaevoConsulta.diagnostico;
      this.pronostico = this.notaevoConsulta.pronostico;
      this.peso = item.paciente.peso;
      this.talla = item.paciente.talla;
      this.alergias = item.paciente.alergias;
      this.edad = item.paciente.edad;
      this.tipo_comprobante = item.tipo_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.paciente = item.paciente._id;
      this.pacientee=item.paciente.nombre;
      this.impuesto = item.impuesto;
      this.observacion_receta = item.observacion_receta
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetalle = 1;
    },
    editarConsulta(item) {
      this.limpiar();
      this.notaevoConsulta = item.notaevolucion[item.notaevolucion.length-1]; 
      this.experiencia = this.notaevoConsulta.experiencia;
      this.diagnostico = this.notaevoConsulta.diagnostico;
      this.pronostico = this.notaevoConsulta.pronostico;
      this.idconsulta = item._id;
      this.peso = item.paciente.peso;
      this.talla = item.paciente.talla;
      this.alergias = item.paciente.alergias;
      this.edad = item.paciente.edad;
      this.tipo_comprobante = item.tipo_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.paciente = item.paciente._id;
      this.pacientee=item.paciente.nombre;
      this.impuesto = item.impuesto;
      this.observacion_receta = item.observacion_receta
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetalle = 0;
      this.editedIndex = 1;

    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("consulta/listhoy?valor=" + this.momentcc, configuracion)
        .then(function(response) {
          me.consultas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    mostrarModalTratamientos() {
      this.dialog = 1;
    },
    limpiar() {
      this._id = "";  
      this.impuesto = 0.18;
      this.codigo = "";
      this.total = 0;
      this.totalParcial = 0;
      this.totalImpuesto = 0;
      this.detalles = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
      this.paciente = "";
      this.pacientee = "";
      this.observacion_receta = "";
      this.experiencia = "";
      this.diagnostico = "";
      this.pronostico = "";
      this.receta = [];
      this.pacientes = [];
      this.arrayNotaEvo =[];
      this.notaevoArray =[];
    
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un tratamiento al detalle");
        this.arrayNotaEvo =[]
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }

      return this.valida;
    },
    mostrarNuevo(item) {
      this.verNuevo = 1;
      this.pacienteee=item._id;
      this.seleccionarPacienteModal = 0;
      this.pacientee=item.nombre;
      this.edad=item.edad;
      this.peso=item.peso;
      this.talla=item.talla;
      this.alergias=item.alergias;
      this.notaevoArray = item.notaevolucion[item.notaevolucion.length-1]; 
    },
    seleccionarPaciente() {
      this.limpiar();
      this.seleccionarPacienteModal = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },
    guardar() {
      let me = this;
      var coleccion = {createAb:new Date(),experiencia:this.experiencia, diagnostico:this.diagnostico, pronostico:this.pronostico}
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      this.arrayNotaEvo.push(coleccion);
      if (this.validar()) {
        return;
      }
      //despues del token 
      if (this.editedIndex > -1) {
      axios
        .put(
          "consulta/update",
          {
            _id: this.idconsulta,
            notaevolucion: this.arrayNotaEvo,
            
          },
          configuracion
        )
      axios
      .put(
            "paciente/updateNotaEvo",
            {
              _id: this.paciente,
              notaevolucion: this.arrayNotaEvo          
            },
            configuracion
          )
        .then(function(response) {
          me.limpiar();
          me.close();
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
         } else {
           axios
        .post(
          "consulta/add",
          {
            paciente: this.pacienteee,
            usuario: this.$store.state.usuario._id,
            num_comprobante: this.num_comprobante,
            impuesto: this.impuesto,
            total: this.total,
            detalles: this.detalles,
            observacion_receta: this.observacion_receta,
            receta: this.receta,
            notaevolucion: this.arrayNotaEvo,
            saldo:this.total,
            tipo:"MEDICO"
          },
          configuracion
        )
      axios
      .put(
            "paciente/updateNotaEvo",
            {
              _id: this.pacienteee,
              notaevolucion: this.arrayNotaEvo          
            },
            configuracion
          )
        .then(function(response) {
          me.limpiar();
          me.close();
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
      }
    },

    close() {
      this.dialog = false;
    }
  }
};
</script>

