                                              <td style="text-align:right;">{{formatPrice(det.descuento)}}</td>
                                              <td style="text-align:right;">{{formatPrice((det.cantidad*det.precio)-det.descuento)}}</td>
                                          </tr>
                                      </tbody>
                                      <tfoot>
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">TOTAL</th>
                                              <th style="text-align:right;">$ {{formatPrice(totalconsulta)}}</th>
                                          </tr>
                                          
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">ABONO</th>
                                              <th style="text-align:right;">-$ {{formatPrice(abonohist)}}</th>
                                          </tr>
                                         
                                         
                                      </tfoot>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <br>
                          <footer>
                              <div id="gracias">
                                  <p><b>Gracias por su preferencia!</b></p>  
                              </div>
                          </footer>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="ocultarComprobantepasado()" class="ma-2" tile outlined color="error">Cancelar</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
            
            
            <v-data-table 
                :headers="headers"
                :items="consultas"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0"
            >
                <template v-slot:item.opciones="{item}">
                     <div v-if="item.tipo != 'TERAPIA' ">
                        <v-btn  small color="success" class="ma-3 white--text" @click="mostrarnuevoPago(item)"><v-icon small dark>find_in_page</v-icon>
                    </v-btn>
                    </div>
                    <div v-else>
                       <v-btn  small color="warning" class="ma-3 white--text" @click="mostrarnuevoPago(item)"><v-icon small dark>find_in_page</v-icon>
                    </v-btn>
                    </div>
                </template>
                <template v-slot:item.lafecha="{item}">
