<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar v-if="verNuevo == 0" text color="white">
        <v-toolbar-title>Corte de caja</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>
        <v-spacer></v-spacer>
        <v-text-field
          v-if="verNuevo == 0"
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details
        >
        </v-text-field>
        <v-spacer></v-spacer>
        <v-btn @click="generarCorte()">
          <v-icon>point_of_sale</v-icon>
        </v-btn>
      </v-toolbar>

      <!--  ....................Seccion de Modales........................... -->

      <!--  ....................modal para corte........................... -->
      <v-dialog v-model="comprobanteCorte" max-width="1000px">
        <v-card>
          <v-card-title class="headline" justify="right">
            <v-btn @click="crearPDFcorte()"><v-icon>print</v-icon></v-btn> Corte de
            caja
          </v-card-title>
          <v-card-text>
            <div id="facturacorte">
              <header>
                <div id="logo">
                  <img
                    src="data:image/jpeg;base64,"
                    id="imagen"
                  />
                </div>
                <div id="datos">
                  <p id="encabezado">
                    <b>Medicina Muscular</b><br />Av.Camara de Comercio por 58 y
                    48 No.317, Villas la Hacienda, Merida<br />Telefono:9991775676<br />
                  </p>
                </div>
                <div id="fact">
                  <p>
                    {{ format_dateFull(new Date()) }}
                  </p>
                </div>
              </header>
              <br />
              <section>
                <div>
                  <table id="fapaciente">
                    <tbody>
                      <tr>
                        <td id="paciente">
                          Responsable del corte:
                          <strong> {{ this.$store.state.usuario.email }}</strong
                          ><br />
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </section>
              <br />
              <section>
                <div>
                  <table id="factratamientos">
                    <thead>
                      <tr id="fa">
                        <th>PAGOS REALIZADOS EN EFECTIVO</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody></tbody>
                    <tfoot>
                      <tr>
                        <th style="text-align: right">TOTAL</th>
                        <th style="text-align: right">
                          $ {{ (total = calcularTotal).toFixed(2) }}
                        </th>
                      </tr>
                    </tfoot>
                  </table>

                  <table id="factratamientos">
                    <thead>
                      <tr id="fa">
                        <th>PAGOS REALIZADOS CON TARJETA</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody></tbody>
                    <tfoot>
                      <tr>
                        <th style="text-align: right">TOTAL</th>
                        <th style="text-align: right">
                          $
                          {{ (totaltarjeta = calcularTotaltarjeta).toFixed(2) }}
                        </th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </section>
              <br />
              <br />
              <footer>
                <div id="gracias">
                  INGRESOS TOTALES:
                  <p>$ {{ (total = calcularTotall).toFixed(2) }}</p>
                </div>
              </footer>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn
              @click="ocultarComprobante()"
              class="ma-2"
              tile
              outlined
              color="error"
              >Cancelar</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-data-table
        :headers="headers"
        :items="listados"
        :search="search"
        class="elevation-1"
        v-if="verNuevo == 0"
      >
        <template v-slot:item.fecha="{ item }">
          <div>
            {{ format_dateFull(item.createdAt) }}
          </div>
        </template>
        <template v-slot:item.paciente="{ item }">
          <div>
            <span>{{ item.paciente }}</span>
          </div>
        </template>
        <template v-slot:item.forma="{ item }">
          <div>
            <span>{{ item.tipo }}</span>
          </div>
        </template>
        <template v-slot:item.monto="{ item }">
          <div>
            <span>{{ formatPrice(item.monto) }}</span>
          </div>
        </template>

        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Resetear</v-btn>
        </template>
      </v-data-table>

      <!-- Termina seccion para agregar una nueva consulta o ver una consulta -->
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
import moment from "moment";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import autoTable from "jspdf-autotable";
export default {
  data() {
    return {
      abonos: [],
      date: new Date().toISOString().substr(0, 10),
      saldobase: [],
      dialog: false,
      cantidad_pagar: "",
      momentcc: moment().startOf("day"),
      saldo: "",
      search: "",
      forma: "",
      totalconsulta: "",
      listados: [],
      efectivos: [],
      tarjetas: [],
      abonitos: [],
      headers: [
        { text: "Fecha", value: "fecha", sortable: true },
        { text: "Paciente", value: "paciente", sortable: true },
        { text: "Forma de pago", value: "forma", sortable: true },
        { text: "Monto", value: "monto", sortable: true },
      ],
      _id: "",
      id: "",
      saldooo: [],
      pacientee: "",
      pacienteee: "",
      paciente: "",
      alergias: "",
      edad: "",
      pacientes: [],
      nuevaconsulta: [],
      listaAbonos: [],
      arrayAbono: [],
      arraySaldo: [],
      tipo_comprobante: "",
      idconsulta: "",
      comprobantes: ["FACTURA", "TICKET"],
      num_comprobante: "",
      experiencia: "",
      nombre: "",
      diagnostico: "",
      pronostico: "",
      medicamento: "",
      descripcion: "",
      observacion_receta: "",
      impuesto: 0.18,
      codigo: "",

      detalles: [],

      receta: [],
      notaevolucion: [],
      verNuevo: 0,
      errorTratamientos: null,
      errorMedicamentos: null,
      total: 0,
      totaltarjeta: 0,
      totalParcial: 0,
      totalImpuesto: 0,
      tratamientos: [],
      texto: "",
      verDetalle: 0,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      comprobanteCorte: 0,
      hoja: 0,
      seleccionarPacienteModal: 0,
      fecha: null,
    };
  },
  filters: {
    currencydecimal(total) {
      return total.toFixed(2);
    },
  },
  computed: {
    calcularTotal: function () {
      let resultado = 0.0;
      for (var i = 0; i < this.efectivos.length; i++) {
        resultado = resultado + this.efectivos[i].monto;
      }
      return resultado;
    },
    calcularTotall: function () {
      let resultado = 0.0;
      for (var i = 0; i < this.listados.length; i++) {
        resultado = resultado + this.listados[i].monto;
      }
      return resultado;
    },
    calcularTotaltarjeta: function () {
      let resultadoo = 0.0;
      for (var i = 0; i < this.tarjetas.length; i++) {
        resultadoo = resultadoo + this.tarjetas[i].monto;
      }
      return resultadoo;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  created() {
    this.listar();

    /* this.selectPacientes(); */
  },
  methods: {
    crearPDF() {
      var columns = [
        { title: "Fecha", dataKey: "fecha" },
        { title: "Paciente", dataKey: "paciente" },
        { title: "Forma de Pago", dataKey: "tipo" },
        { title: "Monto", dataKey: "monto" },
      ];

      var fechi = this.format_dateFull(new Date());
      var rows = [];

      this.listados.map(function (x) {
        rows.push({
          fecha: x.createdAt,
          paciente: x.paciente,
          tipo: x.tipo,
          monto: x.monto,
        });
      });
      var doc = new jsPDF("p", "pt");
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        addPageContent: function (data) {
          doc.text("Corte de caja " + fechi, 40, 30);
        },
      });

      doc.save("IngresosGenerales.pdf");
    },

    crearPDFcorte() {
      var quotes = document.getElementById("facturacorte");
      html2canvas(quotes).then(function (canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 200;
        var pageHeight = 255;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("CorteCaja.pdf");
      });
    },

    format_date(value) {
      if (value) {
        moment.locale("es");
        return moment(String(value)).format("LL");
      }
    },
    format_dateFull(value) {
      if (value) {
        moment.locale("es");
        return moment(String(value)).format("LLLL");
      }
    },
    formatPrice(value) {
      let val = (value / 1).toFixed(2).replace(/[^\d\.]/g, "");
      return val.toString().replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,");
    },

    formatDate(date) {
      const [day, month, year] = date.split("-");
      return `${day}/${month}/${year}`;
    },

    generarCorte() {
      this.comprobanteCorte = 1;
    },

ocultarComprobante() {
      this.comprobanteCorte = 0;
    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("pagos/list?valor=" + this.momentcc, configuracion)

        .then(function (response) {
          me.listados = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });

      axios
        .get("pagos/listefectivo?valor=" + this.momentcc, configuracion)

        .then(function (response) {
          me.efectivos = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });

      axios
        .get("pagos/listarjeta?valor=" + this.momentcc, configuracion)

        .then(function (response) {
          me.tarjetas = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    mostrarnuevoPago() {
      this.listaAbonos = item.abonos;
    },
  },
};
</script>

<style>
#facturacorte {
  padding: 20px;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

#contenido {
  font-family: Arial, sans-serif;
  font-size: 16px;
  width: 100%;
  height: 100%;
}

#logo {
  float: left;
  margin-left: 2%;
  margin-right: 2%;
}

#logoReceta {
  float: right;
  margin-left: 1%;
  margin-right: 1%;
}

#imagen {
  width: 100px;
}
#imagenReceta {
  width: 120px;
}

#fact {
  font-size: 18px;
  font-weight: bold;
  text-align: center;
}

#datos {
  float: left;
  margin-top: 0%;
  margin-left: 2%;
  margin-right: 2%;
  text-align: justify;
}
#datosReceta {
  float: center;
  margin-top: 0%;
  margin-left: 2%;
  margin-right: 2%;
}

#encabezado {
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 16px;
}
#encabezadoReceta {
  text-align: left;
  margin-left: -25px;
  margin-right: 10px;
  font-size: 12px;
}

section {
  clear: left;
}

#paciente {
  text-align: left;
}

#fapaciente {
  width: 100%;
}

#fa {
  color: #ffffff;
  font-size: 14px;
}

#factratamientos {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  padding: 20px;
  margin-bottom: 15px;
}

#factratamientos thead {
  padding: 20px;
  background: #1bbc9b;
  text-align: center;
  border-bottom: 1px solid #ffffff;
}
#medicamentos {
  border-collapse: collapse;
  border-spacing: 0;
  padding: 20px;
  margin-bottom: 15px;
  width: 100%;
}

#medicamentos thead {
  padding: 20px;
  background: #1bbc9b;
  text-align: center;
  border-bottom: 1px solid #ffffff;
}
#medicamentos td {
  max-width: 200px;
  text-align: justify;
}

#gracias {
  text-align: center;
}
#botones {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
}
</style>