<template>
    <v-layout align-start>
        <v-flex class="inicio">
            <v-toolbar text color="white">
                <v-toolbar-title>Pacientes</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-if="verNuevo==0,verTodoPaciente==0,verConsulta==0,verTerapia==0" class="text-xs-center" v-model="search" append-icon="search" 
                label="Bsqueda" single-line hide-details></v-text-field>
                <v-spacer></v-spacer>
                  <v-btn color="primary" v-if="verNuevo==0,verTodoPaciente==0,verConsulta==0,verTerapia==0" @click="mostrarNuevo()" dark class="mb-2" >Nuevo</v-btn>

                  <v-dialog v-model="adModal" max-width="290">
                      <v-card>
                          <v-card-title class="headline" v-if="adAccion==1">
                              Activar Item
                          </v-card-title>
                          <v-card-title class="headline" v-if="adAccion==2">
                              Desactivar Item
                          </v-card-title>
                          <v-card-text>
                              Ests a punto de <span v-if="adAccion==1">activar </span>
                              <span v-if="adAccion==2">desactivar </span> el item {{adNombre}}
                          </v-card-text>
                          <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn @click="activarDesactivarCerrar()" class="ma-2" tile outlined color="error">
                                  Cancelar
                              </v-btn>
                              <v-btn v-if="adAccion==1" @click="activar()" class="ma-2" tile outlined color="success">
                                  Activar
                              </v-btn>
                              <v-btn v-if="adAccion==2" @click="desactivar()" class="ma-2" tile outlined color="warning">
                                  Desactivar
                              </v-btn>
                          </v-card-actions>
                      </v-card>
                  </v-dialog>
            </v-toolbar>
            
            <v-data-table
                :headers="headers"
                :items="pacientes"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0,verTodoPaciente==0,verConsulta==0,verTerapia==0"
                
            >
                <template v-slot:item.opciones="{item}">
                  <v-btn  small  @click="verHistorial(item)">
                    <v-icon small>
                    folder_shared
                    </v-icon>
                    </v-btn>
                    <v-btn  style="margin-left: 2px" small  @click="editItem(item)">
                    <v-icon small>
                    edit
                    </v-icon>
                    </v-btn>
                    <template v-if="item.estado">
                      <v-btn  style="margin-left: 2px" small @click="activarDesactivarMostrar(2,item)">
                        <v-icon small>
                        block
                        </v-icon>
                      </v-btn>
                    </template>
                    <template v-else>
                      <v-btn style="margin-left: 2px" small  @click="activarDesactivarMostrar(1,item)">
                        <v-icon small>
                        check
                        </v-icon>
                        </v-btn>
                    </template>
         
                   
                </template>
                
                <template v-slot:items="props">
                <td>{{ props.item.num_control }}</td>
                <td>{{ props.item.nombre }}</td>
                <td>{{ props.item.email }}</td>
                </template>
                <template v-slot:item.estado="{item}">
                    <div v-if="item.estado">
                        <span class="blue--text">Activo</span>
                    </div>
                    <div v-else>
                        <span class="red--text">Inactivo</span>
                    </div>
                </template>             
                
                <template v-slot:no-data>
                <v-btn color="primary" @click="listar()">Resetear</v-btn>
                </template>
            </v-data-table>

 <!-- ....................modal Receta.................... -->
              <v-dialog v-model="hoja" max-width="1000px">
                  <v-card>
                    <v-card-title class="headline" justify="right">
                      <v-btn @click="crearPDFreceta()"><v-icon>print</v-icon></v-btn>     Receta Medica
                    </v-card-title>
                    <v-card-text>
                      <div id="contenidos">
                          <header>
                              <div id="logoReceta">
                                  <img src="data:image/jpeg;base64," id="imagenReceta" >
                              </div>
                              <div id="datosReceta">
                                  <p id="encabezadoReceta">
                                      <b>Dr. Jorge Luis Ojeda Barrientos</b><br>
                                       Mdico Cirujano - Universidad Anhuac Mayab<br>
                                       Especialista en Medicina del Deporte - UADY<br>
                                       Maestria en Fisioterapia Deportiva - Universidad Modelo<br>
                                       Certificado por el Consejo Nacional de Medicina del Deporte<br>
                                       Fecha: <strong>{{date}}</strong>
                                  </p>
                              </div>
                          </header>
                          <br>
                          <section>
                              <div>
                                  <table id="fapaciente">
                                      <tbody>
                                        <v-layout wrap>
                                        <v-flex sm12  md6 lg6 x6>
                                           <strong>
                                            <v-text-field label="Nombre del paciente"  readonly v-model="pacientee" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md2 lg2 x2>
                                           <strong>
                                            <v-text-field label="Edad"  readonly v-model="edad" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md2 lg2 x2>
                                           <strong>
                                            <v-text-field label="Peso"  readonly v-model="peso" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md2 lg2 x2>
                                           <strong>
                                            <v-text-field label="Talla"  readonly v-model="talla" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        <v-flex sm12  md12 lg12 x12>
                                           <strong>
                                            <v-text-field label="Alergias"  readonly v-model="alergias" >
                                            </v-text-field>
                                           </strong>
                                        </v-flex>
                                        </v-layout>
                                      </tbody>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <section>
                              <div>
                                  <table id="medicamentos">
                                      <thead>
                                          <tr id="fa">
                                              <th>MEDICAMENTO</th>
                                              <th>DESCRIPCION</th>
                                          </tr>
                                      </thead>
                                      <tbody>
                                          <tr v-for="det in receta" :key="det._id">
                                              <td style="text-align:center;">{{det.medicamento}}</td>
                                              <td style="text-align:center;">{{det.descripcion}}</td>
                                          </tr>
                                      </tbody>
                                 </table>
                              </div>
                          </section>
                          <br>
                          
                          <footer>
                              <div id="gracias">
                                  <p><b>Gracias por su preferencia!</b></p>
                              </div>
                          </footer>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="ocultarReceta()" class="ma-2" tile outlined color="error">Cancelar</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
    <!-- ............Seccion para ver el Historial del paciente....... -->


          <v-container grid-list-lg class="pa-4 white" v-if="verTodoPaciente == 2">
              <v-layout row wrap>
                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Datos del paciente</strong></v-card-title>
                  </v-card>
                </v-flex>
                
                <v-flex xs12 lg6>
                 <v-text-field v-model="nombre" label="Nombre" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs6 lg3>
                 <v-text-field v-model="edad" label="Edad" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs6 lg3>
                 <v-text-field v-model="sexo" label="Sexo" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg6>
                 <v-text-field v-model="direccion" label="Direccion" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg3>
                 <v-text-field v-model="email" label="Email" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs6 lg3>
                 <v-text-field v-model="telefono" label="Telefono" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs6 lg3>
                 <v-text-field v-model="peso" label="Peso" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs6 lg3>
                 <v-text-field v-model="talla" label="Talla" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Antecedentes Patologicos</strong></v-card-title>
                  </v-card>
                </v-flex>
                <v-flex xs2 lg12>
                  <v-row  justify="space-around">
                    <v-checkbox disabled value="si" v-model="diabetes" class="mx-4" label="Diabetes"></v-checkbox>
                    <v-checkbox disabled value="si" v-model="hipertencion" class="mx-12" label="Hipertencion"></v-checkbox>
                    <v-checkbox disabled value="si" v-model="cancer" class="mx-12" label="Cancer"></v-checkbox>
                  </v-row>
                </v-flex>
                <v-flex xs12 lg4>
                 <v-text-field v-model="cardiaco" label="Cardiaco" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                 <v-text-field v-model="alergias" label="Alergias" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                 <v-text-field v-model="fracturas" label="Fracturas" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                 <v-text-field v-model="hospitalizaciones" label="Hospitalizaciones" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                 <v-text-field v-model="cirugias" label="Cirugias Previas" outlined readonly></v-text-field>
                </v-flex>
                <v-flex xs12 lg4>
                 <v-text-field v-model="medicamentos" label="Medicamentos" outlined readonly></v-text-field>
                </v-flex>

               
                <br><br>
                <br><br>
                <br><br>
                
          <v-flex xs12 sm12 md12 lg12 xl12>
              <v-toolbar color="teal" dark >
                  <v-toolbar-title>Consultas del Paciente</v-toolbar-title>
              </v-toolbar>
