                    >
                     <template  v-slot:item.OpcionesConsultas="{item}">
                  <v-btn  x-small  @click="mostrarConsulta(item)">
                    <v-icon small>
                    folder_shared
                    </v-icon>
                    </v-btn>      
                </template>
                <template v-slot:item.lafecha="{item}">
                    <div>
                        <span>{{format_date(item.createdAt)}}</span>
                    </div>
                </template> 
                    
                    <template v-slot:no-data>
                      <h3>No hay registro de consultas</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

                <v-flex xs12 sm12 md12 lg12 xl12>
              <v-toolbar color="teal" dark >
                  <v-toolbar-title>Terapias del paciente</v-toolbar-title>
              </v-toolbar>
                  <template>
                    <v-data-table
                      :headers="cabeceraDetallesterapia"
                      :items="consultasdeterapia"
                      class="elevation-1"
                    >
                     <template v-slot:item.OpcionesTerapias="{item}">
                  <v-btn  x-small  @click="mostrarTerapia(item)">
                    <v-icon small>
                    folder_shared
                    </v-icon>
                    </v-btn>      
                </template>
                <template v-slot:item.lafech="{item}">
                    <div>
                        <span>{{format_date(item.createdAt)}}</span>
                    </div>
                </template> 
                    
                    <template v-slot:no-data>
                      <h3>No hay registro de terapias</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

                <v-flex xs12 sm12 md12 lg12 xl12>
              <v-toolbar color="teal" dark >
                  <v-toolbar-title>Notas de evolucion</v-toolbar-title>
              </v-toolbar>
                  <template>
                    <v-data-table
                      :headers="cabeceraEvolucion"
                      :items="notaevolucion"
                      class="elevation-1"
                    >

                     <template v-slot:item.lafechaa="{item}">
                    <div>
                        <span>{{format_date(item.createAb)}}</span>
                    </div>
                </template> 
                    
                    <template v-slot:no-data>
                      <h3>No hay registro de consultas</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

                <v-flex xs12 sm12 md12 lg12 xl12>
              <v-toolbar color="teal" dark >
                  <v-toolbar-title>Observaciones Terapeuticas
                  </v-toolbar-title>
              </v-toolbar>
                  <template>
                    <v-data-table
                      :headers="cabeceraTerapeutica"
                      :items="ObservacionesTerapeuticas"
                      class="elevation-1"
                    >
                     <template v-slot:item.lafechaaa="{item}">
                    <div>
                        <span>{{format_date(item.createAc)}}</span>
                    </div>
                </template> 
                    
                    <template v-slot:no-data>
                      <h3>No hay registro de consultas</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

                

               
                <v-flex xs12 sm12 md12 v-show="valida">
                  <div class="red-text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                </v-flex>
                <v-flex xs12 sm12 md12 xl12>
                  <v-btn class="ma-2" tile outlined color="error" @click.native="ocultarNuevo()">Cancelar</v-btn>
                  
                  
                </v-flex>
              </v-layout>
            </v-container>

                  <!-- ............Termina Seccion para ver el Historial del paciente....... -->


          <!-- ............Seccion verTerapia....... -->
<v-container  grid-list-sm class="pa-4 white" v-if="verTerapia == 4">
              <v-layout row wrap>
                <v-flex sm12 sm8 md8 lg6 x18>
                  <v-text-field readonly v-model="pacientee" label="Paciente">
                  </v-text-field>
                </v-flex>
                

                
                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Observaciones Terapeuticas</strong></v-card-title>
                  </v-card>
                </v-flex>
                

                <v-flex xs12 lg12 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="observacionterapia"
                   label="Observaciones"
                  ></v-textarea>
                </v-flex>
            

                <!-- ...................Seccion para realizar una receta................. --> 

                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Receta Medica</strong><v-spacer></v-spacer> <v-btn color="primary" @click="mostrarReceta(),scrollToElement()"><v-spacer></v-spacer>
                <v-icon >print</v-icon>
              </v-btn></v-card-title>
                 
                  </v-card>
                </v-flex>
                
                <v-flex xs12 lg12 >
                   <v-textarea v-model="observacion_receta" class="mx-2" label="Observaciones" rows="1" prepend-icon="create">
                   </v-textarea>
                </v-flex>
                <v-flex xs12 sm2 md2 lg2 xl2  v-show="errorMedicamentos">
                  <div class="red--text" v-text="errorMedicamentos">

                  </div>

                </v-flex>
                 <v-flex xs12 sm12 md12 lg12 xl12>
                  <template>
                    <v-data-table
                      :headers="cabeceraRecetas"
                      :items="receta"
                      hide-default-footer
                      class="elevation-1"
                    >
                  <template v-slot:item.borrar="{item}">
                     <v-icon small class="mr-2">
                      delete
                     </v-icon>
                    </template>
                    <template v-slot:item.medicamento="props">
                     <v-text-field v-model="props.item.medicamento">
                     </v-text-field>
                    </template>
                   <template v-slot:item.descripcion="props">
                    <v-text-field v-model="props.item.descripcion">
                    </v-text-field>
                   </template>
                  <template v-slot:no-data>
                      <h3>No hay detalles</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

           <!-- ...................Seccion para realizar el cobro................. --> 

                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Detalles de cobro</strong></v-card-title>
                  </v-card>
                </v-flex>
                

               
                <v-flex xs12 sm2 md2 lg2 xl2  v-show="errorTratamientos">
                  <div class="red--text" v-text="errorTratamientos">

                  </div>

                </v-flex>
                <v-flex xs12 sm12 md12 lg12 xl12>
                  <template>
                    <v-data-table
                      :headers="cabeceraConsultas"
                      :items="detalles"
                      hide-default-footer
                      class="elevation-1"
                    >
                    <template v-slot:item.borrar="{item}">
                     <v-icon
                    small
                    class="mr-2"
                    >
                    delete
                    </v-icon>
                    
                </template>
                    <template v-slot:item.cantidad="props">
           <v-text-field v-model="props.item.cantidad" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.precio="props">
           <v-text-field v-model="props.item.precio" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.descuento="props">
           <v-text-field v-model="props.item.descuento" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.subtotal="props">
          ${{formatPrice((props.item.cantidad * props.item.precio)-props.item.descuento)}}
         </template>
                    <template v-slot:no-data>
                      <h3>No hay tratamientos agregados al detalle</h3>
                    </template>  
                    </v-data-table>
                   
                    <v-flex class="text-xs-right">
                      <strong></strong> 
                       
                    </v-flex>
                    <v-flex class="text-xs-right">
                      <strong>Total Neto:</strong> $ {{formatPrice(total=calcularTotal)}}
                    </v-flex>
                  </template>
                </v-flex>
                <v-flex xs12 sm12 md12 v-show="valida">
                  <div class="red-text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                </v-flex>
                <v-flex xs12 sm12 md12 xl12>
                  <v-btn class="ma-2" tile outlined color="error" @click.native="ocultarConsulta()">Cancelar</v-btn>
                  
                  
                </v-flex>
              </v-layout>
            </v-container>



            <!-- ............Seccion verconsulta....... -->
<v-container  grid-list-sm class="pa-4 white" v-if="verConsulta == 4">
              <v-layout row wrap>
                <v-flex sm12 sm8 md8 lg6 x18>
                  <v-text-field readonly v-model="pacientee" label="Paciente">
                  </v-text-field>
                </v-flex>
                

                
                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Nota de evolucion</strong></v-card-title>
                  </v-card>
                </v-flex>
                
                 <v-flex xs12 lg4 >
                   <v-textarea
                   
                   background-color="amber lighten-4"
                   filled
                   readonly
                   name="input-7-1"
                   label="Padecimiento Actual"
                   v-model="notaevoArray.experiencia"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea  
                   background-color="amber lighten-4"
                   filled
                   readonly
                   name="input-7-1"
                   label="Diagnostico"
                   v-model="notaevoArray.diagnostico"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea
                   background-color="amber lighten-4"
                   filled
                   readonly
                   name="input-7-1"
                   label="Pronostico"
                   v-model="notaevoArray.pronostico"
                  ></v-textarea>
                </v-flex>
              
                <v-flex xs12 lg4 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="experiencia" 
                   label="Padecimiento Actual"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="diagnostico"
                   label="Diagnostico"
                  ></v-textarea>
                </v-flex>
                <v-flex xs12 lg4 >
                   <v-textarea 
                   name="input-7-1"
                   v-model="pronostico"
                   label="Pronostico"
                  ></v-textarea>
                </v-flex>
            

                <!-- ...................Seccion para realizar una receta................. --> 

                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Receta Medica</strong><v-spacer></v-spacer> <v-btn color="primary" @click="mostrarReceta(),scrollToElement()"><v-spacer></v-spacer>
                <v-icon >print</v-icon>
              </v-btn></v-card-title>
                 
                  </v-card>
                </v-flex>
                
                <v-flex xs12 lg12 >
                   <v-textarea v-model="observacion_receta" class="mx-2" label="Observaciones" rows="1" prepend-icon="create">
                   </v-textarea>
                </v-flex>
                <v-flex xs12 sm2 md2 lg2 xl2  v-show="errorMedicamentos">
                  <div class="red--text" v-text="errorMedicamentos">

                  </div>

                </v-flex>
                 <v-flex xs12 sm12 md12 lg12 xl12>
                  <template>
                    <v-data-table
                      :headers="cabeceraRecetas"
                      :items="receta"
                      hide-default-footer
                      class="elevation-1"
                    >
                  <template v-slot:item.borrar="{item}">
                     <v-icon small class="mr-2">
                      delete
                     </v-icon>
                    </template>
                    <template v-slot:item.medicamento="props">
                     <v-text-field v-model="props.item.medicamento">
                     </v-text-field>
                    </template>
                   <template v-slot:item.descripcion="props">
                    <v-text-field v-model="props.item.descripcion">
                    </v-text-field>
                   </template>
                  <template v-slot:no-data>
                      <h3>No hay detalles</h3>
                    </template>  
                    </v-data-table>
                  </template>
                </v-flex>

           <!-- ...................Seccion para realizar el cobro................. --> 

                <v-flex xs12 lg12 >
                  <v-card color="blue-grey darken-1" dark>
                  <v-card-title class="justify-right"><strong>Detalles de cobro</strong></v-card-title>
                  </v-card>
                </v-flex>
                

               
                <v-flex xs12 sm2 md2 lg2 xl2  v-show="errorTratamientos">
                  <div class="red--text" v-text="errorTratamientos">

                  </div>

                </v-flex>
                <v-flex xs12 sm12 md12 lg12 xl12>
                  <template>
                    <v-data-table
                      :headers="cabeceraConsultas"
                      :items="detalles"
                      hide-default-footer
                      class="elevation-1"
                    >
                    <template v-slot:item.borrar="{item}">
                     <v-icon
                    small
                    class="mr-2"
                    >
                    delete
                    </v-icon>
                    
                </template>
                    <template v-slot:item.cantidad="props">
           <v-text-field v-model="props.item.cantidad" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.precio="props">
           <v-text-field v-model="props.item.precio" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.descuento="props">
           <v-text-field v-model="props.item.descuento" type="number">
           </v-text-field>
         </template>
         <template v-slot:item.subtotal="props">
          ${{formatPrice((props.item.cantidad * props.item.precio)-props.item.descuento)}}
         </template>
                    <template v-slot:no-data>
                      <h3>No hay tratamientos agregados al detalle</h3>
                    </template>  
                    </v-data-table>
                   
                    <v-flex class="text-xs-right">
                      <strong></strong> 
                       
                    </v-flex>
                    <v-flex class="text-xs-right">
                      <strong>Total Neto:</strong> $ {{formatPrice(total=calcularTotal)}}
                    </v-flex>
                  </template>
