<script>
import axios from "axios";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import moment from "moment";
export default {
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      dialog: false,
      search: "",
      pacientes: [],
      editedIndex: -1,
      verConsulta: 0,
      verTerapia: 0,
      observacionterapia: "",
      verNuevo: 0,
      verTodoPaciente: 0,
      notaevonuevoarray: [],
      notaevonuevoarrayterapias: [],
      notaevoArrayterapiaa:[],
      evoluciones: [],
      notaevolucion: [],
      ObservacionesTerapeuticas: [],
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Numero de control", value: "_id", sortable: true },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Email", value: "email", sortable: false },
        { text: "Telefono", value: "telefono", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      _id: "",
      nombre: "",
      tipo_persona: "Paciente",
      sexo: "",
      genero: ["HOMBRE", "MUJER"],
      edad: "",
      num_control: "",
      direccion: "",
      telefono: "",
      email: "",
      estado_civil: "",
      escolaridad: "",
      diabetes: "no",
      hipertencion: "no",
      cancer: "no",
      cardiaco: "",
      alergias: "",
      fracturas: "",
      hospitalizaciones: "",
      cirugias: "",
      medicamentos: "",
      peso: "",
      talla: "",
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      hoja: 0,
      adNombre: "",
      adId: "",
      errorTratamientos: null,
      errorMedicamentos: null,
      consultas: [],
      consultasdeterapia: [],
      detalles: [],
      pacientee: "",
      tipo_comprobante: "",
      comprobantes: ["FACTURA", "TICKET"],
      num_comprobante: "",
      impuesto: 0.18,
      codigo: "",
      detalles: [],
      notaevoArray: [],
      receta: [],
      experiencia: "",
      diagnostico: "",
      pronostico: "",
      medicamento: "",
      descripcion: "",
      observacion_receta: "",
      total: 0,
      totalParcial: 0,
      totalImpuesto: 0,
      cabeceraConsultas: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Tratamiento", value: "tratamientos", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Sub Total", value: "subtotal", sortable: false }
      ],
      cabeceraDetalles: [
        { text: "Opciones", value: "OpcionesConsultas", sortable: false },
        { text: "Tratamiento", value: "tipo", sortable: false },
        { text: "Paciente", value: "paciente.nombre", sortable: false },
        { text: "Num_comprobante", value: "num_comprobante", sortable: false },
        { text: "Fecha de consulta", value: "lafecha", sortable: false }
      ],
      cabeceraDetallesterapia: [
        { text: "Opciones", value: "OpcionesTerapias", sortable: false },
        { text: "Tratamiento", value: "tipo", sortable: false },
        { text: "Paciente", value: "paciente.nombre", sortable: false },
        { text: "Num_comprobante", value: "num_comprobante", sortable: false },
        { text: "Fecha de consulta", value: "lafech", sortable: false }
      ],
      
      cabeceraTerapeutica: [
        { text: "Fecha de terapia", value: "lafechaaa", sortable: false },
        { text: "Observacion", value: "observacionterapia", sortable: false }
       
      ],
      cabeceraEvolucion: [
        { text: "Fecha de consulta", value: "lafechaa", sortable: false },
        { text: "Padecimiento Actual", value: "experiencia", sortable: false },
        { text: "Diagnostico", value: "diagnostico", sortable: false },
        { text: "Pronostico", value: "pronostico", sortable: false }
      
      ],
      cabeceraRecetas: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Medicamento", value: "medicamento", sortable: false },
        { text: "Descripcion", value: "descripcion", sortable: false }
      ]
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo registro" : "Editar registro";
    },
    calcularTotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        resultado =
          resultado +
          (this.detalles[i].cantidad * this.detalles[i].precio -
            this.detalles[i].descuento);
      }
      return resultado;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  created() {
    this.listar();
  },
  methods: {
    format_date(value){
         if (value) {
           moment.locale('es');
           return moment(String(value)).format('LL')
          }
      },
      format_dateFull(value){
         if (value) {
           moment.locale('es');
           return moment(String(value)).format('LLLL');
          }
      },
    formatPrice(value) {
      let val = (value/1).toFixed(2).replace(/[^\d\.]/g, "")
        return val.toString().replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,")
    },
    scrollToElement() {
    const el = this.$el.getElementsByClassName('inicio')[0];

    if (el) {
      el.scrollIntoView();
    }
  },
    crearPDFreceta() {
      var quote = document.getElementById("contenidos");
      html2canvas(quote).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 200;
        var pageHeight = 200;

        var imgHeight = canvas.height * imgWidth / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteConsulta.pdf");
      });
    },

    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("paciente/listPacientes", configuracion)
        .then(function(response) {
          me.pacientes = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarConsultas(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("consulta/queryHist?_id=" + id, configuracion)
        .then(function(response) {
          console.log(response.data);
          me.consultas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });

    axios
        .get("consulta/queryHistterapias?_id=" + id, configuracion)
        .then(function(response) {
          console.log(response.data);
          me.consultasdeterapia = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    mostrarReceta() {
      this.hoja = 1;
    },
    limpiar() {
      this._id = "";
      this.nombre = "";
      this.num_control = "";
      this.edad = "";
      this.sexo = "";
      this.direccion = "";
      this.telefono = "";
      this.email = "";
      this.estado_civil = "";
      this.escolaridad = "";
      this.diabetes = "";
      this.hipertencion = "";
      this.cancer = "";
      this.cardiaco = "";
      this.alergias = "";
      this.fracturas = "";
      this.hospitalizaciones = "";
      this.cirugias = "";
      this.medicamentos = "";
      this.peso = "";
      this.talla = "";
      this.valida = 0;
      this.validaMensaje = [];
      this.editedIndex = -1;
      this.verNuevo = 0;
      this.verTodoPaciente = 0;
      this.verConsulta = 0;
      this.verTerapia = 0;
      this.notaevoArrayterapiaa =[];
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];

      if (this.nombre.length < 1 || this.nombre.length > 50) {
        this.validaMensaje.push("El Nombre del paciente es obligatorio.");
      }
      if (!this.email) {
        this.validaMensaje.push("El Correo electrnico es obligatorio.");
      } else if (!this.validEmail(this.email)) {
        this.validaMensaje.push("El Correo electrnico debe ser vlido.");
      }

      /* if (this.num_control.length > 20) {
        this.validaMensaje.push(
          "La docuemento no debe tener ms de 20 caracteres."
        );
      }
      if (this.direccion.length > 70) {
        this.validaMensaje.push(
          "La direccion no debe tener ms de 70 caracteres."
        );
      }
      if (this.telefono.length > 20) {
        this.validaMensaje.push(
          "El telefono no debe tener ms de 20 caracteres."
        );
      }
      if (this.nombre.length > 50) {
        this.validaMensaje.push(
          "El email del usuario debe tener menos de 50 caracteres."
        );
      } */

      if (this.validaMensaje.length) {
        this.valida = 1;
      }

      return this.valida;
    },
    validEmail: function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    mostrarNuevo() {
      this.verNuevo = 3;
      this.verTodoPaciente = 9;
      this.verConsulta = 9;
      this.verTerapia = 9;
      
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.verTodoPaciente = 0;
      this.verConsulta = 0;
      this.verTerapia = 0;
      this.limpiar();
    },
    ocultarConsulta() {
      this.verConsulta = 9;
      this.verTerapia = 9;
      this.verTodoPaciente = 2;
      this.verNuevo = 9;
      
    },
    ocultarReceta() {
      this.hoja = 0;
    },
    mostrarConsulta(item) {
      this.paciente = item.paciente._id;
      this.pacientee = item.paciente.nombre;
      this.num_comprobante = item.num_comprobante;
      this.tipo_comprobante = item.tipo_comprobante;
      this.peso = item.paciente.peso;
      this.talla = item.paciente.talla;
      this.alergias = item.paciente.alergias;
      this.edad = item.paciente.edad;
      this.impuesto = item.impuesto;
      this.observacion_receta = item.observacion_receta;
      if (item.notaevolucion.length > 0) {
      this.notaevoConsulta = item.notaevolucion[item.notaevolucion.length - 1];
      this.experiencia = this.notaevoConsulta.experiencia;
      this.diagnostico = this.notaevoConsulta.diagnostico;
      this.pronostico = this.notaevoConsulta.pronostico;
      }
      this.listarDetalles(item._id);
      this.verTodoPaciente = 9;
      this.verConsulta = 4;
      this.verNuevo = 9;
      this.editedIndex = -1;
    },
    mostrarTerapia(item) {
      this.paciente = item.paciente._id;
      this.pacientee = item.paciente.nombre;
      this.num_comprobante = item.num_comprobante;
      this.tipo_comprobante = item.tipo_comprobante;
      this.notaevoArrayterapiaa = item.notasterapias[item.notasterapias.length-1];
      this.observacionterapia = this.notaevoArrayterapiaa.observacionterapia;
      this.peso = item.paciente.peso;
      this.talla = item.paciente.talla;
      this.alergias = item.paciente.alergias;
      this.edad = item.paciente.edad;
      this.impuesto = item.impuesto;
      this.observacion_receta = item.observacion_receta;
      if (item.notaevolucion.length > 0) {
      this.notaevoConsulta = item.notaevolucion[item.notaevolucion.length - 1];
      this.experiencia = this.notaevoConsulta.experiencia;
      this.diagnostico = this.notaevoConsulta.diagnostico;
      this.pronostico = this.notaevoConsulta.pronostico;
      }
      this.listarDetalles(item._id);
      this.verTodoPaciente = 9;
      this.verTerapia = 4;
      this.verNuevo = 9;
      this.editedIndex = -1;
    },
    
    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("consulta/query?_id=" + id, configuracion)
        .then(function(response) {
          me.detalles = response.data.detalles;
          me.receta = response.data.receta;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      var coleccion = {
        createAb: new Date(),
        experiencia: "Primera Consulta",
        diagnostico: "Primera Consulta",
        pronostico: "primera Consulta"
      };
      var coleccion2 = {
        createAc: new Date(),
        observacionterapia: "Primera Terapia"
      };
      this.notaevonuevoarray.push(coleccion);
      this.notaevonuevoarrayterapias.push(coleccion2);
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        //Cdigo para editar
        axios
          .put(
            "paciente/update",
            {
              _id: this._id,
              tipo_persona: this.tipo_persona,
              nombre: this.nombre,
              edad: this.edad,
              sexo: this.sexo,
              direccion: this.direccion,
              email: this.email,
              telefono: this.telefono,
              diabetes: this.diabetes,
              hipertencion: this.hipertencion,
              cancer: this.cancer,
              cardiaco: this.cardiaco,
              alergias: this.alergias,
              fracturas: this.fracturas,
              hospitalizaciones: this.hospitalizaciones,
              cirugias: this.cirugias,
              medicamentos: this.medicamentos,
              peso: this.peso,
              talla: this.talla
            },
            configuracion
          )
          .then(function(response) {
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        //Cdigo para guardar
        axios
          .post(
            "paciente/add",
            {
              tipo_persona: this.tipo_persona,
              nombre: this.nombre,
              edad: this.edad,
              sexo: this.sexo,
              direccion: this.direccion,
              email: this.email,
              telefono: this.telefono,
              diabetes: this.diabetes,
              hipertencion: this.hipertencion,
              cancer: this.cancer,
              cardiaco: this.cardiaco,
              alergias: this.alergias,
              fracturas: this.fracturas,
              hospitalizaciones: this.hospitalizaciones,
              cirugias: this.cirugias,
              medicamentos: this.medicamentos,
              peso: this.peso,
              talla: this.talla,
              notaevolucion: this.notaevonuevoarray,
              notasterapias: this.notaevonuevoarrayterapias
            },
            configuracion
          )
          .then(function(response) {
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    editItem(item) {
      this._id = item._id;
      this.nombre = item.nombre;
      this.edad = item.edad;
      this.sexo = item.sexo;
      this.num_control = item.num_control;
      this.direccion = item.direccion;
      this.telefono = item.telefono;
      this.email = item.email;
      this.estado_civil = item.estado_civil;
      this.escolaridad = item.escolaridad;
      this.diabetes = item.diabetes;
      this.hipertencion = item.hipertencion;
      this.cancer = item.cancer;
      this.cardiaco = item.cardiaco;
      this.alergias = item.alergias;
      this.fracturas = item.fracturas;
      this.hospitalizaciones = item.hospitalizaciones;
      this.cirugias = item.cirugias;
      this.medicamentos = item.medicamentos;
      this.peso = item.peso;
      this.talla = item.talla;
      this.dialog = true;
      this.editedIndex = 1;
      this.verNuevo = 3;
      this.verTodoPaciente = 9;
      this.verConsulta = 9;
      this.verTerapia = 9;
    },
    verHistorial(item) {
      this._id = item._id;
      this.limpiar();
      this.verTodoPaciente = 2;
      this.verNuevo = 9;
      this.verConsulta = 9;
      this.verTerapia = 9;
      this.notaevolucion = item.notaevolucion;
      this.ObservacionesTerapeuticas = item.notasterapias;
      this.nombre = item.nombre;
      this.edad = item.edad;
      this.sexo = item.sexo;
      this.paciente = item.paciente;
      this.direccion = item.direccion;
      this.email = item.email;
      this.telefono = item.telefono;
      this.diabetes = item.diabetes;
      this.escolaridad = item.escolaridad;
      this.estado_civil = item.estado_civil;
      this.hipertencion = item.hipertencion;
      this.cancer = item.cancer;
      this.cardiaco = item.cardiaco;
      this.alergias = item.alergias;
      this.fracturas = item.fracturas;
      this.hospitalizaciones = item.hospitalizaciones;
      this.cirugias = item.cirugias;
      this.medicamentos = item.medicamentos;
      this.peso = item.peso;
      this.talla = item.talla;
      console.log(item);
      this.listarConsultas(item._id);
    },

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.nombre;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("paciente/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("paciente/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    close() {
      this.dialog = false;
      this.limpiar();
    }
  }
};
</script>

