import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import Home from '../views/Home.vue'
import Categoria from '../components/Categoria.vue'
import Login from '../components/Login.vue'
import Usuario from '../components/Usuario.vue'
import Tratamientos from '../components/Tratamientos.vue'
import Paciente from '../components/Paciente.vue'
import Consulta from '../components/Consulta.vue'
import PagoConsultas from '../components/pagoconsultas.vue'
import PagosPendientes from '../components/pagospendientes.vue'
import HistorialPagos from '../components/pagoshistorial'
import Terapias from '../components/Terapias.vue'
import Cortecaja from '../components/Cortecaja.vue'
import Consultahistorial from '../components/Consultahistorial.vue'
import Terapiashistorial from '../components/Terapiashistorial.vue'


Vue.use(Router)
var router = new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        administrador: true,
        almacenero: true,
        vendedor: true,
        secretaria:true,
        terapeuta:true,
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        libre: true
      }
    },
    {
      path: '/categoria',
      name: 'categoria',
      component: Categoria,
      meta: {
        administrador: true,
        almacenero: true,
      }
    },
    {
      path: '/tratamientos',
      name: 'tratamientos',
      component: Tratamientos,
      meta: {
        administrador: true,
        almacenero: true,
      }
    },
    {
      path: '/pagoconsultas',
      name: 'pagocontultas',
      component: PagoConsultas,
      meta: {
        administrador: true,
        secretaria:true,
      }
    },
    {
      path: '/pagospendientes',
      name: 'pagospendientes',
      component: PagosPendientes,
      meta: {
        administrador: true,
        secretaria:true,
      }
    },
    {
      path: '/historialpagos',
      name: 'historialpagos',
      component: HistorialPagos,
      meta: {
        administrador: true,
        secretaria:true,
      }
    },
    {
      path: '/paciente',
      name: 'paciente',
      component: Paciente,
      meta: {
        administrador: true,
        secretaria:true,
        terapeuta:true,
      }
    },
    {
      path: '/consulta',
      name: 'consulta',
      component: Consulta,
      meta: {
        administrador: true,
        vendedor: true,
        secretaria:true
      }
    },
    {
      path: '/consultahistorial',
      name: 'consultahistorial',
      component: Consultahistorial,
      meta: {
        administrador: true,
        vendedor: true,
        secretaria:true
      }
    },
    {
      path: '/usuario',
      name: 'usuario',
      component: Usuario,
      meta: {
        administrador: true,
      }
    },
    {
      path: '/terapias',
      name: 'terapias',
      component: Terapias,
      meta: {
        administrador: true,
        terapeuta: true,
        secretaria: true
      }
    },
    {
      path: '/terapiashistorial',
      name: 'terapiashistorial',
      component: Terapiashistorial,
      meta: {
        administrador: true,
        terapeuta: true,
        secretaria: true
      }
    },
    {
      path: '/cortecaja',
      name: 'cortecaja',
      component: Cortecaja,
      meta: {
        administrador: true,
        terapeuta: true,
        secretaria:true,
      }
    }

  ]
})
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.libre)) {
    next();
  } else if (store.state.usuario && store.state.usuario.rol == 'Administrador') {
    if (to.matched.some(record => record.meta.administrador)) {
      next();
    }
  } else if (store.state.usuario && store.state.usuario.rol == 'Secretaria') {
    if (to.matched.some(record => record.meta.secretaria)) {
      next();
    }
  } else if (store.state.usuario && store.state.usuario.rol == 'Asistente') {
    if (to.matched.some(record => record.meta.almacenero)) {
      next();
    }
  } else if (store.state.usuario && store.state.usuario.rol == 'Terapeuta') {
    if (to.matched.some(record => record.meta.terapeuta)) {
      next();
    }
  } else {
    next({ name: 'login' });
  }
})
export default router
