<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar v-if="verNuevo == 0" text color="white">
        <v-toolbar-title>Pagos Pendientes</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-text-field
          v-if="verNuevo == 0"
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
        <v-btn color="info" @click="listar()"><v-icon>cached</v-icon></v-btn>
      </v-toolbar>

      <!--  ....................Seccion de Modales........................... -->
      <!--  ....................modal para el  recibo de una consulta........................... -->
      <v-dialog v-model="comprobanteModal" max-width="1000px">
        <v-card>
          <v-card-text>
            <div id="facturae">
              <header>
                <div id="logo">
                  <img
                    src="data:image/jpeg;base64,"
                    id="imagen"
                  />
                </div>
                <div id="datos">
                  <p id="encabezado">
                    <b>Medicina Muscular</b><br />Av.Camara de Comercio por 58 y
                    48 No.317, Villas la Hacienda, Merida<br />Telefono:9991775676<br />
                  </p>
                </div>
                <div id="fact">
                  <p>
                    {{ formapago }}<br />
                    {{ format_dateFull(new Date()) }}
                  </p>
                </div>
              </header>
              <br />
              <section>
                <div>
                  <table id="fapaciente">
                    <tbody>
                      <tr>
                        <td id="paciente">
                          <strong>Sr(a).{{ paciente.nombre }}</strong
                          ><br />
                          <strong>Direccin:</strong>{{ paciente.direccion
                          }}<br />
                          <strong>Telfono:</strong>{{ paciente.telefono
                          }}<br />
                          <strong>Email:</strong>{{ paciente.email }}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </section>
              <br />
              <section>
                <div>
                  <table id="factratamientos">
                    <thead>
                      <tr id="fa">
                        <th>CANT</th>
                        <th>DESCRIPCION</th>
                        <th>COSTO</th>
                        <th>DESC.</th>
                        <th>PRECIO TOTAL</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="det in detalles" :key="det._id">
                        <td style="text-align: center">{{ det.cantidad }}</td>
                        <td>{{ det.tratamientos }}</td>
                        <td style="text-align: right">
                          {{ formatPrice(det.precio) }}
                        </td>
                        <td style="text-align: right">
                          {{ formatPrice(det.descuento) }}
                        </td>
                        <td style="text-align: right">
                          {{
                            formatPrice(
                              det.cantidad * det.precio - det.descuento
                            )
                          }}
                        </td>
                      </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th style="text-align: right">TOTAL</th>
                        <th style="text-align: right">
                          $ {{ formatPrice(totalconsulta) }}
                        </th>
                      </tr>

                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th style="text-align: right">USTED DEBE</th>
                        <th style="text-align: right">
                          $ {{ formatPrice((totalparcial = saldobase.saldo)) }}
                        </th>
                      </tr>
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th style="text-align: right">ABONO</th>
                        <th style="text-align: right">
                          -$ {{ formatPrice(cantidad_pagar) }}
                        </th>
                      </tr>
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th style="text-align: right">SALDO</th>
                        <th style="text-align:right;">$ {{formatPrice(saldo=((saldobase.saldo)-(cantidad_pagar)))}}</th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </section>
              <br />
              <br />
              <footer>
                <div id="gracias">
                  <p><b>Gracias por su preferencia!</b></p>
                </div>
              </footer>
            </div>
            <div id="botones">
              <v-btn @click="cobrar()" color="success">
                Pagar
                <v-icon right dark>monetization_on</v-icon>
              </v-btn>
            </div>
            <br>
            <div id="botones">
            <v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn
              @click="ocultarComprobante()"
              class="ma-2"
              tile
              outlined
              color="error"
              >Cancelar</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>

  <!--  ....................modal para el  recibo de una consulta pasada........................... -->
      <v-dialog v-model="comprobanteModalPasado" max-width="1000px">
                  <v-card>
                    <v-card-title class="headline" justify="right">
                      <v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>     Recibo de consulta
                    </v-card-title>
                    <v-card-text>
                      <div id="facturaaaa">
                          <header>
                              <div id="logo">
                                  <img src="data:image/jpeg;base64," id="imagen" >
                              </div>
                              <div id="datos">
                                  <p id="encabezado">
                                      <b>Medicina Muscular</b><br>Av.Camara de Comercio por 58 y 48 No.317, Villas la Hacienda, Merida<br>Telefono:9991775676<br>
                                  </p>
                              </div>
                              <div id="fact">
                                  <p>{{tipo_comprobante}}<br>
                                  {{num_comprobante}}<br>
                                  {{format_dateFull(fechaabonohist)}}</p>
                              </div>
                          </header>
                          <br>
                          <section>
                              <div>
                                  <table id="fapaciente">
                                      <tbody>
                                          <tr>
                                              <td id="paciente">
                                                  <strong>Sr(a).{{paciente.nombre}}</strong><br>
                                                  <strong>Direccin:</strong>{{paciente.direccion}}<br>
                                                  <strong>Telfono:</strong>{{paciente.telefono}}<br>
                                                  <strong>Email:</strong>{{paciente.email}}
                                              </td>
                                          </tr>
                                      </tbody>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <section>
                              <div>
                                  <table id="factratamientos">
                                      <thead>
                                          <tr id="fa">
                                              <th>CANT</th>
                                              <th>DESCRIPCION</th>
                                              <th>COSTO</th>
                                              <th>DESC.</th>
                                              <th>PRECIO TOTAL</th>
                                          </tr>
                                      </thead>
                                      <tbody>
                                          <tr v-for="det in detalles" :key="det._id">
                                              <td style="text-align:center;">{{det.cantidad}}</td>
                                              <td>{{det.tratamientos}}</td>
                                              <td style="text-align:right;">{{formatPrice(det.precio)}}</td>
                                              <td style="text-align:right;">{{formatPrice(det.descuento)}}</td>
                                              <td style="text-align:right;">{{formatPrice((det.cantidad*det.precio)-det.descuento)}}</td>
                                          </tr>
                                      </tbody>
                                      <tfoot>
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">TOTAL</th>
                                              <th style="text-align:right;">$ {{formatPrice(totalconsulta)}}</th>
                                          </tr>
                                          
                                          <tr>
                                              <th></th>
                                              <th></th>
                                              <th></th>
                                              <th style="text-align:right;">ABONO</th>
                                              <th style="text-align:right;">-$ {{formatPrice(abonohist)}}</th>
                                          </tr>
                                         
                                         
                                      </tfoot>
                                  </table>
                              </div>
                          </section>
                          <br>
                          <br>
                          <footer>
                              <div id="gracias">
                                  <p><b>Gracias por su preferencia!</b></p>  
                              </div>
                          </footer>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn @click="ocultarComprobantepasado()" class="ma-2" tile outlined color="error">Cancelar</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>

      <v-data-table
        :headers="headers"
        :items="consultas"
        :search="search"
        class="elevation-1"
        v-if="verNuevo == 0"
      >
        <template v-slot:item.opciones="{ item }">
          <div v-if="item.tipo != 'TERAPIA'">
            <v-btn
              small
              color="success"
              class="ma-3 white--text"
              @click="mostrarnuevoPago(item)"
              >Pagar
            </v-btn>
          </div>
          <div v-else>
            <v-btn
              small
              color="warning"
              class="ma-3 white--text"
              @click="mostrarnuevoPago(item)"
              >Pagar
            </v-btn>
          </div>
        </template>
        <template v-slot:item.fecha="{ item }">
          <div>
            <span>{{ format_dateFull(item.createdAt) }}</span>
          </div>
        </template>
        <template v-slot:items="props">
          <td>{{ props.item.usuario.nombre }}</td>
          <td>{{ props.item.paciente.nombre }}</td>
          <td>{{ props.item.tipo_comprobante }}</td>
          <td>{{ props.item.num_comprobante }}</td>
        </template>
        <template v-slot:item.total="{ item }">
          <div>
            <span>{{ formatPrice(item.total) }}</span>
          </div>
        </template>
        <template v-slot:item.saldo="{ item }">
          <div>
            <span>{{ formatPrice(item.saldo) }}</span>
          </div>
        </template>

        <template v-slot:item.estado="{ item }">
          <div v-if="item.estado != 3">
            <span class="red--text">Pendiente</span>
          </div>
          <div v-else>
            <span class="green--text">Pagado</span>
          </div>
        </template>

        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Resetear</v-btn>
        </template>
      </v-data-table>

      <!--  ....................Generar un pago........................... -->

      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
        <v-card>
          <v-system-bar></v-system-bar>
          <v-toolbar flat>
            <v-toolbar-title>{{ paciente.nombre }}</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-banner single-line>
            <strong> Informacion de pago</strong>
          </v-banner>
          <v-card-text class="grey lighten-4">
            <v-sheet max-width="800" class="mx-auto">
              <br />
              <v-row dense>
                <v-col cols="10" max-width="500" class="mx-auto">
                  <v-card color="#385F73" dark>
                    <v-card-title class="headline"
                      >Total: ${{ formatPrice(saldobase.saldo) }}
                    </v-card-title>
                    <v-card-subtitle
                      >Consulta realizada el dia
                      {{ format_dateFull(fecha) }}.</v-card-subtitle
                    >
                    <v-col cols="6" class="mx-auto">
                      <v-text-field
                        type="number"
                        v-model="cantidad_pagar"
                        label="Total a Pagar"
                        prefix="$"
                      >
                      </v-text-field>
                    </v-col>
                    <v-card-actions>
                      <v-flex xs6 sm4 md4>
                        <v-select
                          v-model="formapago"
                          :items="items"
                          item-text="state"
                          item-value="abbr"
                          label="Select"
                          single-line
                        ></v-select>
                      </v-flex>
                      <div v-if="formapago == 'EFECTIVO'">
                        <v-btn text @click="generarCobro()">
                          COBRAR
                          <v-icon right dark>payments</v-icon>
                        </v-btn>
                      </div>

                      <div v-else-if="formapago == 'TARJETA'">
                        <v-btn text @click="generarCobro()">
                          COBRAR
                          <v-icon right dark>payments</v-icon>
                        </v-btn>
                      </div>

                      <div v-else>
                        <v-btn disabled text @click="generarCobro()">
                          COBRAR
                          <v-icon right dark>payments</v-icon>
                        </v-btn>
                      </div>
                    </v-card-actions>
                  </v-card>
                </v-col>
              </v-row>
              <v-layout row wrap>
                <v-sheet width="700" max-height="900" class="mx-auto">
                  <br />
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <table id="factratamientospago">
                      <thead>
                        <tr id="fa">
                          <th>CANT</th>
                          <th>DESCRIPCION</th>
                          <th>COSTO</th>
                          <th>DESC.</th>
                          <th>PRECIO TOTAL</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="det in detalles" :key="det._id">
                          <td style="text-align: center">{{ det.cantidad }}</td>
                          <td>{{ det.tratamientos }}</td>
                          <td style="text-align: center">
                            {{ formatPrice(det.precio) }}
                          </td>
                          <td style="text-align: center">
                            {{ formatPrice(det.descuento) }}
                          </td>
                          <td style="text-align: center">
                            {{
                              formatPrice(
                                det.cantidad * det.precio - det.descuento
                              )
                            }}
                          </td>
                        </tr>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align: right">TOTAL</th>
                          <th style="text-align: center">
                            $ {{ formatPrice(totalconsulta) }}
                          </th>
                        </tr>
                      </tbody>
                    </table>
                  </v-flex>
                </v-sheet>
              </v-layout>

              <v-sheet max-width="700" max-height="900" class="mx-auto">
                <br />
                <v-flex xs12 sm12 md12 lg12 xl12>
                  <v-toolbar color="#385F73" dark>
                    <v-toolbar-title>Pagos realizados</v-toolbar-title>
                  </v-toolbar>
                  <template>
                    <v-data-table
                      :headers="cabeceraDetalles"
                      :items="listaAbonos"
                      class="elevation-1"
                    >
                      <template v-slot:item.OpcionesHistorial="{ item }">
                        <v-btn x-small @click="verHistorial(item)">
                          <v-icon small> folder_shared </v-icon>
                        </v-btn>
                      </template>
                      <template v-slot:item.fechaa="{ item }">
                        <div>
                          <span>{{ format_date(item.createAb) }}</span>
                        </div>
                      </template>
                      <template v-slot:item.abonoo="{ item }">
                        <div>
                          <span>{{ formatPrice(item.abono) }}</span>
                        </div>
                      </template>
                      <template v-slot:item.saldoo="{ item }">
                        <div>
                          <span>{{ formatPrice(item.saldo) }}</span>
                        </div>
                      </template>

                      <template v-slot:no-data>
                        <h3>No hay Registro de pagos</h3>
                      </template>
                    </v-data-table>
                  </template>
                </v-flex>
              </v-sheet>
              <br />
              <br />
              <div id="botones">
                <v-btn @click="ocultarSalir()" color="error">
                  Salir
                  <v-icon right dark>close</v-icon>
                </v-btn>
              </div>
              <br />
            </v-sheet>
          </v-card-text>
        </v-card>
      </v-container>

      <!-- Termina seccion para agregar una nueva consulta o ver una consulta -->
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import moment from "moment";
export default {
  data() {
    return {
      date: new Date().toISOString().substr(0, 10),
      saldobase: [],
      formapago: { state: "Forma de pago", abbr: "Forma de pago" },
      items: [
        { state: "Efectivo", abbr: "EFECTIVO" },
        { state: "Tarjeta", abbr: "TARJETA" },
        { state: "Forma de pago", abbr: "Forma de pago" },
      ],
      listaAbonos: [],
      abonos: [],
      estadoActualiza: "",
      dialog: false,
      cantidad_pagar: "",
      saldo: "",
      momentcc: moment().startOf("day"),
      search: "",
      totalconsulta: "",
      consultas: [],
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Folio", value: "num_comprobante", sortable: false},
        { text: "Fecha", value: "fecha", sortable: true },
        { text: "Tratamiento", value: "tipo", sortable: true },
        { text: "Paciente", value: "paciente.nombre", sortable: true },
        { text: "Total", value: "total", sortable: false },
        { text: "Saldo", value: "saldo", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
      ],
      _id: "",
      id: "",
      saldooo: [],
      pacientee: "",
      pacienteee: "",
      paciente: "",
      alergias: "",
      edad: "",
      pacientes: [],
      nuevaconsulta: [],
      arrayAbono: [],
      arraySaldo: [],
      tipo_comprobante: "",
      idconsulta: "",
      comprobantes: ["FACTURA", "TICKET"],
      num_comprobante: "",
      experiencia: "",
      nombre: "",
      diagnostico: "",
      pronostico: "",
      medicamento: "",
      descripcion: "",
      observacion_receta: "",
      impuesto: 0.18,
      codigo: "",
      cabeceraDetalles: [
        { text: "Opciones", value: "OpcionesHistorial", sortable: false },
        { text: "Fecha", value: "fechaa", sortable: false },
        { text: "Abono", value: "abonoo", sortable: false },
        { text: "Saldo", value: "saldoo", sortable: false },
      ],
      detalles: [],
      receta: [],
      notaevolucion: [],
      verNuevo: 0,
      errorTratamientos: null,
      errorMedicamentos: null,
      total: 0,
      totalParcial: 0,
      totalImpuesto: 0,
      tratamientos: [],
      texto: "",
      cabeceraTratamientos: [
        { text: "Seleccionar", value: "opcioness", sortable: false },
        { text: "Codigo", value: "codigo", sortable: true },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Categoria", value: "categoria.nombre", sortable: true },
        { text: "Precio", value: "precio_venta", sortable: false },
        { text: "Descripcin", value: "descripcion", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
      ],
      cabeceraPacientes: [
        { text: "Seleccionar", value: "opcion", sortable: false },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Email", value: "email", sortable: true },
        { text: "Telefono", value: "telefono", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
      ],
      verDetalle: 0,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      abonohist: "",
      fechaabonohist: "",
      saldohist: "",
      comprobanteModal: 0,
      comprobanteModalPasado: 0,
      hoja: 0,
      seleccionarPacienteModal: 0,
      fecha: null,
    };
  },
  filters: {
    currencydecimal(total) {
      return total.toFixed(2);
    },
  },
  computed: {
    calcularTotal: function () {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        resultado =
          resultado +
          (this.detalles[i].cantidad * this.detalles[i].precio -
            this.detalles[i].descuento);
      }
      return resultado;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  created() {
    this.listar();
    /* this.selectPacientes(); */
  },
  methods: {
    format_date(value) {
      if (value) {
        moment.locale("es");
        return moment(String(value)).format("LL");
      }
    },
    format_dateFull(value) {
      if (value) {
        moment.locale("es");
        return moment(String(value)).format("LLLL");
      }
    },
    formatPrice(value) {
      let val = (value / 1).toFixed(2).replace(/[^\d\.]/g, "");
      return val.toString().replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,");
    },
    crearPDF() {
      var quotes = document.getElementById("facturae");
      html2canvas(quotes).then(function (canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 200;
        var pageHeight = 255;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteConsulta.pdf");
      });
    },

    cobrar() {
      let me = this;
      var coleccionabono = {
        createAb: new Date(),
        abono: this.cantidad_pagar,
        saldo: this.saldo,
      };
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      this.arrayAbono.push(coleccionabono);
      if (this.saldo == 0) {
        this.estadoActualiza = "3";
      } else {
        this.estadoActualiza = "2";
      }
      axios.put(
        "consulta/updatesaldo",
        {
          _id: this.idconsulta,
          abonos: this.arrayAbono,
          estado: this.estadoActualiza,
          saldo: this.saldo,
        },
        configuracion
      );
      axios
        .post(
          "pagos/add",
          {
            monto: this.cantidad_pagar,
            paciente: this.paciente.nombre,
            tipo: this.formapago,
          },
          configuracion
        )
        .then(function (response) {
          me.ocultarComprobante();
          me.limpiar();
          me.close();
          me.listar();
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    verHistorial(item) {
      this.comprobanteModalPasado = 1;
      this.abonohist = item.abono;
      this.fechaabonohist = item.createAb;
      this.saldohist = item.saldo;
    },    

    formatDate(date) {
      const [day, month, year] = date.split("-");
      return `${day}/${month}/${year}`;
    },

    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("consulta/query?_id=" + id, configuracion)
        .then(function (response) {
          me.detalles = response.data.detalles;
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    ocultarComprobante() {
      this.comprobanteModal = 0;
    },
    ocultarSalir() {
      this.limpiar();
    },

    /*  selectPacientes() {
      let me = this;
      let pacienteArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("paciente/listPacientes", configuracion)
        .then(function(response) {
          pacienteArray = response.data;
          pacienteArray.map(function(x) {
            me.pacientes.push({ text: x.nombre,  value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    }, */

    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("consulta/listPendientes", configuracion)
        .then(function (response) {
          me.consultas = response.data;
          me.abonos = response.data.abonos;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    mostrarModalTratamientos() {
      this.dialog = 1;
    },
    limpiar() {
      this._id = "";
      this.tipo_comprobante = "";
      this.num_comprobante = "";
      this.impuesto = 0.18;
      this.codigo = "";
      this.total = 0;
      this.totalParcial = 0;
      this.totalImpuesto = 0;
      this.detalles = [];
      this.saldobase = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
      this.paciente = "";
      this.observacion_receta = "";
      this.experiencia = "";
      this.diagnostico = "";
      this.pronostico = "";
      this.receta = [];
      this.arrayAbono = [];
      this.arraySaldo = [];
      this.diagnostico = "";
      this.pronostico = "";
      this.experiencia = "";
      this.cantidad_pagar = "";
      this.saldo = "";
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];

      if (!this.tipo_comprobante) {
        this.validaMensaje.push("Seleccione un tipo de comprobante");
      }
      if (!this.num_comprobante) {
        this.validaMensaje.push("Ingrese el numero del comprobante");
      }
      if (!this.impuesto || this.impuesto < 0 || this.impuesto > 1) {
        this.validaMensaje.push("Ingrese un impuesto valido.");
      }
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un tratamiento al detalle");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }

      return this.valida;
    },

    mostrarnuevoPago(item) {
      this.totalconsulta = item.total;
      if (Object.keys(item.abonos).length == 0) {
        item.abonos.push({ saldo: item.total });
      }
      this.verNuevo = 1;
      this.idconsulta = item._id;
      this.paciente = item.paciente;
      this.listaAbonos = item.abonos;
      this.saldobase = item.abonos[item.abonos.length - 1];
      this.tipo_comprobante = item.tipo_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.fecha = item.createdAt;
      this.tipo = item.tipo;
      this.impuesto = item.impuesto;
      this.listarDetalles(item._id);
    },

    generarCobro() {
      
      this.comprobanteModal = 1;
    
    },
    seleccionarPaciente() {
      this.limpiar();
      this.seleccionarPacienteModal = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },
    close() {
      this.dialog = false;
    },
  },
};
</script>

